@font-face {
    font-family: 'VT323';
    font-style: normal;
    font-weight: 400;
    src: local('VT323 Regular'), local('VT323-Regular'), url(https://fonts.gstatic.com/s/vt323/v10/pxiKyp0ihIEF2isfFJA.ttf) format('truetype');
  }
  * {
    box-sizing: border-box;
  }
  body {
    background: #383838;
    color: #14fdce;
    font-family: "VT323";
    font-size: 24px;
    -webkit-font-smoothing: none;
    line-height: 1.2;
  }
  h1 {
    font-size: 4rem;
    margin-bottom: 1rem;
    text-transform: uppercase;
  }
  p {
    margin-bottom: 2rem;
  }
  a,
  a:visited {
    border-bottom: 0.2rem solid #14fdce;
    /* color: #14fdce; */
    text-decoration: none;
  }
  button {
    border: 1px black;
  }
  a:active,
  a:focus,
  a:hover {
    background-color: #14fdce;
    color: #031e11;
  }
  .piece {
    display: block;
    height: 100%;
    left: 0;
    top: 0;
    width: 100%;
  }
  .noclick {
    pointer-events: none;
  }
  .frame {
    background-color: transparent;
    border-radius: 2rem;
    border: 1rem solid;
    border-bottom-color: #121212;
    border-left-color: #080808;
    border-right-color: #080808;
    border-top-color: #020202;
    box-shadow: inset 0 0 18rem black, inset 0 0 3rem black, 0 0 10rem black;
    height: 96%;
    position: absolute;
    top: 2%;
    left: 2%;
    width: 96%;
    pointer-events: none;
  }
  .emblem {
    display: none;
    position: absolute;
    left: 50%;
    bottom: 0;
    font-size: 2rem;
    height: 3rem;
    width: 3rem;
    text-align: center;
    color: white;
    background: -webkit-linear-gradient(#fff, #555);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow: 0 0.1rem 0rem rgba(0, 0, 0, 0.4), 0 0 2rem rgba(0, 0, 0, 0.8);
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
  }
  .output {
    -webkit-animation: crt-output 10ms infinite;
            animation: crt-output 10ms infinite;
    background-color: #031e11;
    overflow: scroll;
    position: absolute;
    padding: 3rem 2rem;
    pointer-events: auto;
    text-shadow: 0rem 0.2rem 1rem #0c7b46;
    z-index: -1;
  }
  @-webkit-keyframes crt-output {
    0% {
      opacity: 0.9;
    }
    50% {
      opacity: 1;
    }
  }
  @keyframes crt-output {
    0% {
      opacity: 0.9;
    }
    50% {
      opacity: 1;
    }
  }
  .scanlines {
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0) 50%, rgba(0, 0, 0, 0.2) 70%, rgba(0, 0, 0, 0.6));
    background-size: 100% 0.3rem;
    border-radius: 2rem;
    position: absolute;
  }
  .glow {
    -webkit-animation: crt-glow 60s infinite;
            animation: crt-glow 60s infinite;
    background: radial-gradient(circle at center, #1bd459 0%, rgba(27, 212, 89, 0.88) 58%, rgba(21, 235, 92, 0.57) 80%, rgba(19, 94, 29, 0.27) 93%, rgba(10, 23, 12, 0) 100%);
    opacity: 0.15;
    pointer-events: none;
    position: fixed;
  }
  @-webkit-keyframes crt-glow {
    0% {
      opacity: 0.1;
    }
    50% {
      opacity: 0.2;
    }
  }
  @keyframes crt-glow {
    0% {
      opacity: 0.1;
    }
    50% {
      opacity: 0.2;
    }
  }
  ::-moz-selection {
    background-color: #14fdce;
    color: #031e11;
  }
  ::selection {
    background-color: #14fdce;
    color: #031e11;
  }
  @font-face {
    font-family: "uuxsl";
    src: url("https://i.icomoon.io/public/188ac2dd2d/VOX/uuxsl.eot");
  }
  @font-face {
    font-family: "uuxsl";
    src: url(data:application/x-font-ttf;charset=utf-8;base64,) format("truetype"), url(data:application/font-woff;charset=utf-8;base64,) format("woff");
    font-weight: normal;
    font-style: normal;
  }
  ::-moz-selection [class^="icon-"],
  ::-moz-selection [class*=" icon-"] {
    font-family: "uuxsl";
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  ::selection [class^="icon-"],
  ::selection [class*=" icon-"] {
    font-family: "uuxsl";
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  ::-moz-selection .icon-information:before {
    content: "\e626";
  }
  ::selection .icon-information:before {
    content: "\e626";
  }
  ::-moz-selection .noisy {
    background-image: url(data:image/png;base64,);
  }
  ::selection .noisy {
    background-image: url(data:image/png;base64,);
  }

  /* background: radial-gradient(circle at center, #1bd459 0%, rgba(27, 212, 89, 0.88) 58%, rgba(21, 235, 92, 0.57) 80%, rgba(19, 94, 29, 0.27) 93%, rgba(10, 23, 12, 0) 100%); */
  .btn {
    padding: 5px;
    margin: 5px;
    color: black;
    background: #1bd459;
    text-decoration: none;
    text-transform: uppercase;
    border-radius: 0%;
    text-shadow: none;
    font-weight: bold;
    border: 2px solid #93EDF5;
    -webkit-box-shadow: 0px 0px 4px 2px rgba(27, 212, 89, 0.88);
    -moz-box-shadow: 0px 0px 4px 2px rgba(27, 212, 89, 0.88);
    box-shadow: 0px 0px 4px 2px rgba(27, 212, 89, 0.88);
  }
  .btn:hover {
    background: transparent;
    cursor: pointer;
    color: #1bd459;
    text-shadow: 0px 0px 5px  rgba(27, 212, 89, 0.88);
  }

  .blinkMe {
    animation: blinker 1s linear infinite;
    color: red;
  }
  
  @keyframes blinker {
    50% {
      opacity: 0;
    }
  }
  

  ._off .frame {
    -webkit-animation: shutdown 400ms linear;
    animation: shutdown 400ms linear;
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
  }
  
  ._off .frame:after {
    -webkit-animation: shutdown-opa 400ms linear;
    animation: shutdown-opa 400ms linear;
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
  }
  
  ._off .tv-panel__indicators SPAN { background: #8f8875; }
  
   @-webkit-keyframes 
  shutdown {  0% {
   -webkit-transform: scale3d(1, 1, 1);
   transform: scale3d(1, 1, 1);
  }
   20% {
   -webkit-transform: scale3d(1, 1.6, 1);
   transform: scale3d(1, 1.6, 1);
  }
   50% {
   -webkit-transform: scale3d(1, 0.005, 1);
   transform: scale3d(1, 0.005, 1);
  }
   100% {
   -webkit-transform: scale3d(0, 0, 1);
   transform: scale3d(0, 0, 1);
  }
  }
   @keyframes 
  shutdown {  0% {
   -webkit-transform: scale3d(1, 1, 1);
   transform: scale3d(1, 1, 1);
  }
   20% {
   -webkit-transform: scale3d(1, 1.6, 1);
   transform: scale3d(1, 1.6, 1);
  }
   50% {
   -webkit-transform: scale3d(1, 0.005, 1);
   transform: scale3d(1, 0.005, 1);
  }
   100% {
   -webkit-transform: scale3d(0, 0, 1);
   transform: scale3d(0, 0, 1);
  }
  }
  @-webkit-keyframes 
  shutdown-opa {  0% {
   opacity: 0;
  }
   50% {
   opacity: 1;
  }
   100% {
   opacity: 1;
  }
  }
  @keyframes 
  shutdown-opa {  0% {
   opacity: 0;
  }
   50% {
   opacity: 1;
  }
   100% {
   opacity: 1;
  }
  }